import React, { useEffect, useState } from 'react';
import parse from "html-react-parser"
import Axios from 'axios';



function Estravismo () {
  
  const [content, setContent] = useState ('')
  const [page, /* setPage */] = useState('estravismo')

  
  useEffect (() => {
    Axios.get("https://still-escarpment-33811.herokuapp.com")
    .then(response => {
      console.log(response.data[page])
      setContent(response.data[page])
      console.log(page)
    })
    
  }, [page]);

  return (
    <div>
      <h1 className='development'>Estravismo</h1>
      <div className='lorems'>
        {parse(content)}     
      </div>
    </div>
    

  );

}

export default Estravismo