import React, { useEffect, useState } from 'react';
import parse from "html-react-parser";
import Axios from 'axios';



function Cataratas() {
  const [content, setContent] = useState('')
  const [page, /* setPage */] = useState('cataratas')


  useEffect (() => {
    Axios.get("https://still-escarpment-33811.herokuapp.com/content")
    .then(response => {
      console.log(response.data[page])
      setContent(response.data[page])
      console.log(response.data)
    })
    
  }, [page]);



  return (
    <div>
      <h1 className='marketing'>Cataratas</h1>
      <div className='lorems'>
        {parse(content)}       
      </div>
    </div>
    
  );
}

export default  Cataratas;
